body {
  margin: 0;
  padding: 0;
  background: #29225D;
  color: #FAFAFA;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
form {
  width: 90%;
  max-width: 300px;
  border-radius: 10px;
  background-color: #4D42A4;
  padding: 33px 40px;
  display: flex;
  margin: 0 auto;
  margin-top: 50px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #fafafa;
  margin-bottom: 26px;
}

textarea {
  background-color: #DEE6ED;
  border: 1.5px solid #202329;
  border-radius: 5px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  
  width: calc(100% - 25px);
}

textarea::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  color: black;
  font-weight: 600;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  background: #61DAFA;
  border-radius: 7px;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel {
  color: #fafafa;
  background: transparent;
}
.TodoCounter{
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}
.TodoCounter--loading{
    opacity: 50%;
}
.TodoSearch {
    background: #DEE6ED;
    border-radius: 10px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #61DAFA;
  }
  .TodoSearch:disabled {
    opacity: 50%;
  }
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.CreateTodoButton {
    z-index: 1;
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
}
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
.TodoItem {
    background-color: #7A69FD;
    color:  black;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
  }

  .TodoItem-p--completed {
    text-decoration: line-through;
  }
  
  .Icon {
    color: antiquewhite;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 0px;
  }
  .Icon-check--active {
    color: #99FF90;
  }
  
  .Icon-delete {
    position: absolute;
    top: -8px; 
    right: 0;
  }
  .Icon-check:hover{
    color: #99FF90;
  }
  .Icon-delete:hover {
    color: red;
  }
.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    /* background-color: #CCC; */
  }
  .Icon-container--check {
    position: absolute;
    left: 0px;
  }
  .Icon-container--edit {
    position: absolute;
    top: -5px;
    right: 48px;
  }
  .Icon-container--delete {
    position: absolute;
    top: -5px; 
    right: 0;
  }
  
  .Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
  }
  .Icon-container:hover .Icon-svg--check {
    fill: #99FF90;
  }
  .Icon-container:hover .Icon-svg--edit {
    fill: #61DAFA;
  }
  .Icon-container:hover .Icon-svg--delete {
    fill: red;
  }
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.Loading-container {
    background-color: #7A69FD;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
  
.Loading-text {
    color:  black;
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
}
  
  .Loading-completeIcon,
  .Loading-deleteIcon {
    border-radius: 50px;
    height: 30px;
    width: 30px;
  }
  
  .Loading-completeIcon {
    position: absolute;
    left: 12px;
  }
  
  .Loading-deleteIcon {
    position: absolute;
    top: -4px;
    right: 0;
  }
  
  .Loading-container,
  .Loading-completeIcon,
  .Loading-deleteIcon {
    background: linear-gradient(90deg, #7A69FD, #29225D);
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
  }
  
  @-webkit-keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }

.crea-container{
    background-color:transparent;
    border-radius: 10px;
    position: relative;
    display: flexbox;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
}
.crea-text {
    text-align: center;
    margin: 0;
    padding-top: 2px;
    color: aliceblue;
    font-size: 24px;
    font-weight: bold;
    line-height: 24px;
}
.add-text{
    text-align: center;
    margin: 0;
    padding-top: 5px;
    color: aliceblue;
    font-size: 16px;
    font-weight: bold;
    line-height: 24px;
}
.AddButton{
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 30%;
    font-weight: bold;
    color: #FAFAFA;
    font-size: 24px;
}
.img-container { 
    text-align: center;
}
img { 
    height: 300px;
    width: 300px;
}
